ease-in-out;\n  }\n}\n","$color-white: #fff;\n$color-main-background: #f8f8f8;\n$color-main-turquoise: #41f6d7;\n$color-stroke-turquoise: #b8ffec;\n$color-secondary-turquoise: #d9fff5;\n$color-stroke-grey: #eaeaea;\n$color-dark-grey: #b5b6bc;\n$color-main-pink: #ff3e79;\n$color-secondary-black: rgba(224, 224, 224, 0);\n$text-color-main-black: #1b1c21;\n$text-color-main-grey: #7e7e82;\n","/* БЛОК САЙДБАР МЕНЮ */\n// ОБЩЕЕ\n.sidebar-menu {\n  z-index: 2;\n  width: 320px;\n  position: fixed;\n  background-color: $color-white;\n  box-shadow: 16px 0px 52px 0px rgba(14, 24, 80, 0.2), -2px 0px 4px 0px rgba(69, 79, 126, 0.02);\n  overflow-y: auto;\n  left: -150%;\n  transition: left 500ms ease-in-out;\n\n  &--open {\n    height: 100%;\n    left: 0;\n  }\n\n  &--close {\n    background-image: url(\"./img/burgerActive.svg\");\n  }\n\n  @media (min-width: 360px) {\n    min-width: 360px;\n  }\n\n  @media (min-width: 768px) {\n    min-width: 320px;\n    width: 320px;\n  }\n\n  @media (min-width: 1366px) {\n    display: block;\n    position: static;\n    box-shadow: none;\n    z-index: 1;\n  }\n\n  /* ШАПКА */\n  &__header {\n    padding: 18.73px 16px 0 16px;\n\n    @media (min-width: 360px) {\n      padding: 18.73px 24px 0 24px;\n    }\n\n    @media (min-width: 768px) {\n      padding: 18.73px 24px 0 24px;\n    }\n\n    @media (min-width: 1366px) {\n      padding-top: 34px;\n    }\n  }\n\n  &__header-container {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n  }\n\n  &__header-container-part1 {\n    display: flex;\n    flex-direction: row;\n    gap: 20px;\n  }\n\n  &__header-container-part2 {\n    display: flex;\n    flex-direction: row;\n  }\n\n  &__logo {\n    background-image: url(./img/logo.svg);\n    background-repeat: no-repeat;\n    width: 96.548px;\n    height: 50.815px;\n  }\n\n  &__header-button {\n    width: 40px;\n    height: 40px;\n    flex-shrink: 0;\n    border: none;\n    border-radius: 50%;\n    background-color: #ff3e79;\n    background-position: center;\n    /* margin-top: 5.73px; */\n    background-image: url(\"./img/glass.svg\");\n    background-repeat: no-repeat;\n\n    &:focus,\n    &:hover {\n      opacity: 0.6;\n      transition: 0.2s ease-in-out;\n      cursor: pointer;\n    }\n  }\n\n  .button-menu--close {\n    background-image: url(\"./img/burgerActive.svg\");\n    background-repeat: no-repeat;\n    margin-top: 7px;\n  }\n\n  /* НАВИГАЦИЯ */\n  &__navigation {\n    margin: 26px 0 24px 0;\n    padding: 0 16px 0 16px;\n\n    @media (min-width: 360px) {\n      margin-top: 24px;\n      padding: 0 24px 0 24px;\n    }\n\n    @media (min-width: 768px) {\n      margin: 50.27px 0 268px 0;\n    }\n  }\n\n  &__list {\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n    margin-left: -16px;\n    gap: 10px;\n\n    @media (min-width: 360px) {\n      margin-left: -24px;\n    }\n  }\n\n  &__item {\n    padding-top: 4px;\n    padding-bottom: 4px;\n\n    @media (min-width: 768px) {\n      padding-top: 7px;\n      padding-bottom: 7px;\n    }\n\n    &:focus,\n    &:hover {\n      content: \"\";\n      width: 4px;\n      background-color: $color-main-turquoise;\n      border-radius: 0px 10px 10px 0px;\n      white-space: nowrap;\n      transition: 0.2s ease-in-out;\n    }\n  }\n\n  &__link {\n    color: $text-color-main-black;\n    font-family: TT Lakes;\n    font-size: 16px;\n    font-style: normal;\n    font-weight: 500;\n    line-height: 150%;\n    letter-spacing: -0.2px;\n    cursor: pointer;\n    padding: 0 0 0 16px;\n\n    @media (min-width: 360px) {\n      padding: 0 0 0 24px;\n    }\n\n    &:focus,\n    &:hover {\n      color: $text-color-main-grey;\n      transition: 0.2s ease-in-out;\n    }\n  }\n\n  /* ПОДВАЛ */\n  &__footer {\n    padding: 0 16px 32px 16px;\n\n    @media (min-width: 360px) {\n      padding: 0 24px 32px 24px;\n    }\n  }\n\n  &__footer-button {\n    width: 40px;\n    height: 40px;\n    flex-shrink: 0;\n    border: none;\n    border-radius: 50%;\n    background-color: #ff3e79;\n    background-position: center;\n    background-image: url(\"./img/body.svg\");\n    background-repeat: no-repeat;\n\n    &:focus,\n    &:hover {\n      opacity: 0.6;\n      transition: 0.2s ease-in-out;\n      cursor: pointer;\n  }\n}\n\n.sidebar-modal-call__button--open {\n  background-image: url(\"./img/phone.svg\");\n}\n\n.sidebar-modal-feedback__button--open {\n  background-image: url(\"./img/mail.svg\");\n}\n\n  &__footer-list {\n    display: flex;\n    justify-content: flex-start;\n    gap: 16px;\n  }\n\n  &__footer-contact {\n    display: flex;\n    flex-direction: column;\n    justify-content: flex-start;\n    gap: 12px;\n    margin: 16px 0 8px 0;\n\n    @media (min-width: 768px) {\n      margin-bottom: 40px;\n    }\n  }\n\n  &__number {\n    font-family: TT Lakes;\n    font-size: 24px;\n    font-style: normal;\n    font-weight: 700;\n    cursor: pointer;\n  }\n\n  &__mail {\n    cursor: pointer;\n  }\n\n  &__footer-button {\n    &:focus,\n    &:hover {\n      opacity: 0.6;\n      transition: 0.2s ease-in-out;\n      cursor: pointer;\n    }\n  }\n\n  &__footer-lengBtn {\n    color: #1b1c21;\n    font-family: \"TT Lakes\";\n    font-size: 16px;\n    font-style: normal;\n    font-weight: 500;\n    line-height: 24px; /* 150% */\n    letter-spacing: -0.2px;\n    border: none;\n    background-color: white;\n    padding: 0;\n  }\n  \n  &__footer-lengBtn:hover {\n    color: #7e7e82;\n    font-family: \"TT Lakes\";\n    font-size: 16px;\n    font-style: normal;\n    font-weight: 500;\n    line-height: 24px; /* 150% */\n    letter-spacing: -0.2px;\n    transition: 0.3s;\n  }\n}\n\n/* ПОЯВЛЕНИЕ САЙДБАР МЕНЮ ПОСЛЕ НАЖАТИЯ НА КНОПКУ МЕНЮ */\n.button-menu--open {\n  height: 100%;\n  left: 0;\n}\n\n/* ПОЯВЛЕНИЕ САЙДБАР МЕНЮ ПОСЛЕ 1440 ПХ+ */\n.button-menu--close {\n  @media (min-width: 1366px) {\n    display: none;\n  }\n}\n\n","/* МОДАЛЬНОЕ ОКНО - ПОЗВОНИТЬ И ОБРАТНАЯ СВЯЗЬ */\n/* ОБЩЕЕ */\n.modal-window {\n  padding: 24px 16px 24px 16px;\n  position: fixed;\n  overflow-y: auto;\n  height: 100%;\n  top: 0;\n  right: -150%;\n  background-color: $color-white;\n  z-index: 3;\n  width: 288px;\n  transition: right 500ms ease-in-out;\n  box-shadow: 16px 0px 52px 0px rgba(14, 24, 80, 0.2), -2px 0px 4px 0px rgba(69, 79, 126, 0.02);\n\n  @media (min-width: 1440px) {\n    width: 520px;\n    display: flex;\n    justify-content: right;\n    padding: 0;\n    background-color: $color-secondary-black;\n    box-shadow: none;\n  }\n\n  &__wrapper {\n    @media (min-width: 1440px) {\n      width: 376px;\n      padding: 24px 32px 24px 32px;\n      box-shadow: 16px 0px 52px 0px rgba(14, 24, 80, 0.2), -2px 0px 4px 0px rgba(69, 79, 126, 0.02);\n      background: $color-white;\n      z-index: 3;\n    }\n  }\n}\n\n.modal {\n  &__title {\n    margin: 40px 0 24px 0;\n    color: $text-color-main-black;\n    font-family: TT Lakes;\n    font-size: 24px;\n    font-style: normal;\n    font-weight: 700;\n    line-height: 32px; /* 133.333% */\n    letter-spacing: -0.6px;\n\n    &::before {\n      content: \"\";\n      position: absolute;\n      width: 4px;\n      height: 40px;\n      top: 105px;\n      left: 0px;\n      border-radius: 0 10px 10px 0;\n      background-color: $color-main-turquoise;\n    }\n\n    @media (min-width: 1440px) {\n      margin-top: 16px;\n      &::before {\n        top: 35px;\n        left: 80px;\n      }\n    }\n  }\n\n  &__form {\n    display: flex;\n    flex-direction: column;\n  }\n\n  &__input {\n    min-width: 88%;\n    height: 22px;\n    padding: 12px 16px;\n    border-radius: 8px;\n    border: 1px solid $color-stroke-grey;\n    background: $color-white;\n    margin-bottom: 16px;\n    font-family: \"TTLakes\", Helvetica, sans-serif;\n    font-size: 16px;\n    font-weight: 500;\n    line-height: 150%;\n    letter-spacing: -0.2px;\n\n    &:hover,\n    &:focus {\n      border-color: $color-main-turquoise;\n      outline: 0;\n    }\n  }\n\n  &__information {\n    max-width: 288px;\n    margin-bottom: 18px;\n    color: text-color-main-grey;\n    font-family: TT Lakes;\n    font-size: 14px;\n    font-style: normal;\n    font-weight: 500;\n    line-height: 16px; /* 114.286% */\n  }\n\n  &__link {\n    color: $color-main-pink;\n    font-family: TT Lakes;\n    font-size: 14px;\n    font-style: normal;\n    font-weight: 500;\n    line-height: 16px;\n  }\n\n  &__button {\n    display: flex;\n    gap: 10px;\n    align-self: flex-end;\n    align-items: center;\n    min-width: 136px;\n    height: 32px;\n    padding: 4px 12px 4px 18px;\n    background-color: $color-main-pink;\n    border-radius: 16px;\n    color: $color-white;\n    font-size: 12px;\n    font-style: normal;\n    font-weight: 700;\n    line-height: 200%;\n    letter-spacing: -0.15px;\n    text-transform: uppercase;\n    background-image: url(\"./img/dropdown.svg\");\n    background-repeat: no-repeat;\n    background-position: right 17px bottom 11px;\n\n    &:hover,\n    &:focus {\n      opacity: 0.6;\n    }\n\n    &--close {\n      width: 40px;\n    height: 40px;\n    flex-shrink: 0;\n    border: none;\n    border-radius: 50%;\n    background-color: #ff3e79;\n    background-position: center;\n    /* margin-top: 5.73px; */\n    background-image: url(\"./img/burgerActive.svg\");\n    background-repeat: no-repeat;\n\n      &:hover,\n      &:focus {\n        opacity: 0.6;\n      }\n\n      @media (min-width: 1440px) {\n        position: absolute;\n        left: 3px;\n        top: 32.5px;\n      }\n    }\n  }\n}\n\n/* ПОЯВЛЕНИЕ МОДАЛКИ -ПОЗВОНИТЬ- ПОСЛЕ НАЖАТИЯ НА КНОПКУ */\n.modal-call {\n  &--open {\n    right: 0;\n  }\n}\n\n/* МОДАЛЬНОЕ ОКНО -ОБРАТНАЯ СВЯЗЬ- */\n\n.modal-feedback {\n  &__messages {\n    height: 118px;\n  }\n  /* ПОЯВЛЕНИЕ МОДАЛКИ ПОСЛЕ НАЖАТИЯ НА КНОПКУ */\n  &--open {\n    right: 0;\n  }\n}\n","*,\nh1,\nh3,\n::before,\n::after {\n  padding: 0;\n  margin: 0;\n  border: 0;\n  font-family: \"TT Lakes\", sans-serif;\n  font-size: 14px;\n  list-style-type: none;\n  text-decoration: none;\n}\n\nbutton {\n  background-color: transparent;\n  cursor: pointer;\n  background: none;\n}\n\nbody {\n  background: $color-main-background;\n}\n\n.body-flex {\n  display: flex;\n}\n","/* ОБЕРТКА САЙТА*/\n\n.section {\n  &__all-content {\n    min-width: 320px;\n  }\n}\n\n/* ШАПКА САЙТА */\n\n.header {\n  height: 88px;\n  display: flex;\n  align-items: center;\n  border-bottom: solid;\n  border-color: #d9fff5;\n  border-bottom-width: 1px;\n  background-color: #fff;\n  justify-content: space-between;\n}\n\n.header__btn {\n  width: 40px;\n  height: 40px;\n  flex-shrink: 0;\n  border: none;\n  border-radius: 50%;\n  background-color: #ff3e79;\n  background-position: center;\n}\n\n.header__btn--burger {\n  background-image: url(./img/burger.svg);\n  background-repeat: no-repeat;\n  padding: 0;\n}\n\n.header__btn--repair {\n  background-image: url(./img/repair.svg);\n  background-repeat: no-repeat;\n  margin-right: 16px;\n  padding: 0;\n}\n\n.header__btn--search {\n  background-image: url(./img/search.svg);\n  background-repeat: no-repeat;\n  padding: 0;\n}\n\n.header__btn--phone {\n  background-image: url(./img/phone.svg);\n  background-repeat: no-repeat;\n}\n\n.header__btn--mail {\n  background-image: url(./img/mail.svg);\n  background-repeat: no-repeat;\n}\n\n.header__btn--body {\n  background-image: url(./img/body.svg);\n  background-repeat: no-repeat;\n}\n\n.header__leftZone {\n  display: flex;\n  align-items: center;\n  margin-left: 16px;\n}\n\n.header__middleZone {\n  display: none;\n}\n\n.header__rightZone {\n  display: flex;\n  margin-right: 20px;\n  align-items: center;\n}\n\n.header__rightZone::before {\n  content: url(./img/divider.svg);\n  margin-right: 24px;\n  height: 32px;\n}\n\n.header__logo {\n  width: 96.548px;\n  height: 50.815px;\n  flex-shrink: 0;\n  border: none;\n  background-color: white;\n  padding: 0;\n  margin-left: 16px;\n}\n\n.header__logo--img {\n  background-image: url(./img/logo.svg);\n  background-repeat: no-repeat;\n  width: 96.548px;\n  height: 50.815px;\n}\n\n@media (min-width: 767px) and (max-width: 1119px) {\n  .header {\n    padding: 0 8px;\n  }\n\n  .header__MR {\n    display: flex;\n    align-items: center;\n  }\n\n  .header__middleZone {\n    display: flex;\n    align-items: center;\n    margin-right: 20px;\n  }\n\n  .header__rightZone {\n    margin-right: 25px;\n  }\n\n  .header__btn--mail {\n    margin-left: 16px;\n  }\n\n  .header__btn--body {\n    margin-left: 16px;\n  }\n\n  .header__logo {\n    margin-left: 42px;\n  }\n\n  .header__btn--burger::after {\n    content: url(./img/divider.svg);\n    margin-left: 60px;\n    height: 32px;\n  }\n}\n\n@media (min-width: 1366px) {\n  .header {\n    display: none;\n  }\n}","/* ПЕРВАЯ СЕКЦИЯ MAIN-SECTION */\n\n.section-main {\n  margin-bottom: 24px;\n}\n\n.services__highlight {\n  background-image: url(./img/highlight.svg);\n  width: 4px;\n  height: 40px;\n  position: absolute;\n  margin-top: 24px;\n}\n\n.services__highlightBR {\n  display: none;\n}\n\n.services__container {\n  display: flex;\n  justify-content: space-between;\n  margin-right: 30px;\n}\n\nh1 {\n  color: #1b1c21;\n  font-family: TT Lakes;\n  font-size: 28px;\n  font-style: normal;\n  font-weight: 700;\n  line-height: 40px; /* 142.857% */\n  letter-spacing: -0.6px;\n  margin: 0;\n  padding-top: 24px;\n  padding-left: 16px;\n}\n\n.services__btns {\n  display: flex;\n}\n\n.services .header__btn:nth-child(2) {\n  display: none;\n}\n\n.services .header__btn:nth-child(4) {\n  display: none;\n}\n\n.services__btn {\n  display: none;\n}\n\n@media (min-width: 1366px) {\n  .services {\n    padding-top: 42px;\n  }\n\n  h1 {\n    padding-top: 0;\n    padding-left: 30px;\n  }\n\n  .services__highlight {\n    display: none;\n  }\n\n  .services__highlightBR {\n    background-image: url(./img/highlightBR.svg);\n    display: initial;\n    position: absolute;\n    width: 3px;\n    height: 40px;\n    margin-top: 0;\n  }\n\n  .services .header__btn:nth-child(2) {\n    display: initial;\n  }\n\n  .services .header__btn:nth-child(4) {\n    display: initial;\n  }\n\n  .header__btn--repair {\n    margin-right: 30px;\n  }\n\n  .services__btn {\n    display: initial;\n    width: 125px;\n    color: #1b1c21;\n    font-family: TT Lakes;\n    font-size: 16px;\n    font-style: normal;\n    font-weight: 500;\n    line-height: 24px; /* 150% */\n    letter-spacing: -0.2px;\n    border: none;\n    background-color: #f8f8f8;\n    padding: 0;\n    margin-right: 10px;\n  }\n}\n\n.navigation {\n  display: flex;\n  margin-left: 16px;\n  margin-top: 24px;\n  overflow: auto;\n  -webkit-overflow-scrolling: touch;\n  scrollbar-width: none;\n  -ms-overflow-style: none;\n}\n\n.navigation::-webkit-scrollbar {\n  height: 0;\n  width: 0px;\n}\n\n.navigation__btn {\n  height: 40px;\n  flex-shrink: 0;\n  border-radius: 6px;\n  background-color: #f8f8f8;\n  color: #7e7e82;\n  font-family: TT Lakes;\n  font-size: 16px;\n  font-style: normal;\n  font-weight: 500;\n  line-height: 24px; /* 150% */\n  letter-spacing: -0.2px;\n  border: none;\n  outline: none;\n  padding-right: 17px;\n  padding-left: 17px;\n}\n\n.navigation__btn:hover {\n  border: 2px solid #b8ffec;\n  background: #fff;\n}\n\n@media (min-width: 767px) and (max-width: 1119px) {\n  .navigation {\n    margin-top: 32px;\n    margin-left: 24px;\n  }\n\n  .navigation__btn--prices {\n    display: initial;\n  }\n}\n\n@media (min-width: 1366px) {\n  .navigation {\n    flex-wrap: wrap;\n    margin-top: 8.37px;\n    margin-left: 32px;\n  }\n\n  .navigation__btn:nth-child(3) {\n    display: none;\n  }\n}\n\n/* ТЕКСТ СТРАНИЦЫ И КАРТИНКА */\n.section-main__content {\n  /* display: flex;\n  flex-wrap: wrap; */\n  display: flex;\n  flex-direction: column;\n\n  @media (min-width: 768px) {\n    display: flex;\n    flex-direction: row;\n    margin: 0 auto;\n    width: 95%;\n  }\n}\n\n.section-main__content-wrapper {\n  display: flex;\n  flex-direction: column;\n  gap: 16px;\n  margin: 0 auto;\n  width: 90%;\n  padding-top: 18px;\n}\n\n.section-m